.popup-add__container {
    max-width: 430px;
    min-width: 240px;
    width: 100%;
    background: #FFFFFF;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    position: relative;
    padding: 34px 36px 36px 36px;
    box-sizing: border-box;
  }

  @media screen and (max-width: 480px) {
    .popup-add__container {
      padding: 30px 20px;
      margin: 0 40px;
    }
  }

